<template>
	<view class="content">
		<view class="logo-bg-box">
			<view class="logo-bg-box-first">
				<view class="login-bg-item-top"></view>
				<view class="login-bg-item-bottom"></view>
			</view>
			<view class="logo-bg-box-second">
				<view class="login-bg-item-top"></view>
				<view class="login-bg-item-bottom"></view>
			</view>
		</view>
		<view class="logo-box">
			<view class="logo-item">四史资讯</view>
			<button @tap="goLogin" class="logo-btn">
				<view class="weixin iconfont iconweix"></view>账号登陆
			</button>
			<view @tap="cancelLogin" class="cancel-login">暂不登录，返回首页</view>
			<view class="login-title-info">登录即代表您同意《用户协议》和《隐私政策》</view>
		</view>
		<u-popup v-model="show" mode="bottom" :closeable="true">
			<view class="login-form">
				<u-form ref="uForm" :label-style="labelStyle">
					<u-form-item label="账号 :">
						<u-input placeholder="请输入账号" v-model="username" />
					</u-form-item>
					<u-form-item label="密码 :">
						<u-input type="password" placeholder="请输入密码" v-model="password" />
					</u-form-item>
				</u-form>
				<button type="primary" @tap="login">登陆</button>
				<view class="links">
					<view class="link-highlight" @tap="gotoRegistration">注册账号</view>
				</view>
			</view>
		</u-popup>
		<u-popup v-model="registShow" mode="bottom" :closeable="true">
			<view class="login-form">
				<u-form ref="uForm" :label-style="labelStyle">
					<u-form-item label="账号 :" label-width="120">
						<u-input placeholder="请输入账号" v-model="reusername" />
					</u-form-item>
					<u-form-item label="密码 :" label-width="120">
						<u-input type="password" placeholder="请输入密码" v-model="repassword" />
					</u-form-item>
				</u-form>
				<button type="primary" @tap="registra">注册</button>
				<view class="links" @tap="gotoLogin">已有账号？<view class="link-highlight">点此登陆</view>
				</view>
			</view>
		</u-popup>
	</view>

</template>

<script>
	import {
		Request
	} from '@/api/request.js'
	export default {
		data() {
			return {
				show: false,
				passShow: false,
				registShow: false,
				labelStyle: {
					color: "#434343",
				},
				username: '',
				password: '',
				reusername: '',
				repassword: '',
				captchaImg: '',
			}
		},
		methods: {
			goLogin: function() {
				this.show = true;
			},
			cancelLogin: function() {
				uni.switchTab({
					url: '../index/index'
				})
			},
			gotoForgetPassword: function() {
				this.show = false;
				this.passShow = true;
			},
			gotoRegistration: function() {
				this.show = false;
				this.registShow = true;
			},
			gotoLogin() {
				this.registShow = false;
				this.show = true;
			},
			login: function() {
				if (this.username == '') {
					uni.showToast({
						title: '请输入用户名',
						icon: 'error'
					})
					return;
				} else if (this.password == '') {
					uni.showToast({
						title: '请输入密码',
						icon: 'error'
					})
					return;
				} else if (this.password.length < 6) {
					uni.showToast({
						title: '密码应大于6位',
						icon: 'error'
					})
					return;
				} else {
					let data = {
						username: this.username,
						password: this.password,
					}
					Request({
							url: "user/login",
							method: "post",
							data: data
						}).then(res => {
							uni.setStorageSync('user', res.data);
							uni.showToast({
								title: '登录成功'
							});
							uni.switchTab({
								url: '../mine/mine'
							});
							console.log(res);
						})
						.catch(cat => {
							console.log(cat);
						});
				}
			},
			registra: function() {
				if (this.reusername == '') {
					uni.showToast({
						title: '请输入用户名',
						icon: 'error'
					})
					return;
				} else if (this.repassword == '') {
					uni.showToast({
						title: '请输入密码',
						icon: 'error'
					})
					return;
				} else if (this.repassword.length < 6) {
					uni.showToast({
						title: '密码应大于6位',
						icon: 'error'
					})
					return;
				} else {
					let data = {
						username: this.reusername,
						password: this.repassword,
					}
					Request({
							url: "user/register",
							method: "post",
							data: data
						}).then(res => {
							uni.showToast({
								title: '注册成功，请前往登录'
							});
							this.gotoLogin();
							console.log(res);
						})
						.catch(cat => {
							console.log(cat);
						});
				}
			},
		}
	}
</script>

<style lang="scss" scoped>
	$color-primary: #d83632;

	.content {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		background-color: #000;
	}

	.logo-bg-box {
		width: 100%;
		height: 9482rpx;
		opacity: .5;
		animation: move 60s linear infinite;

		.logo-bg-box-first,
		.logo-bg-box-second {
			width: 100%;
			height: 50%;
		}

		.login-bg-item-top {
			width: 100%;
			height: 50%;
			background: url(../../static/images/310.jpg);
			background-size: 100% 60%;
			filter: blur(2rpx);
		}

		.login-bg-item-bottom {
			width: 100%;
			height: 50%;
			background: url(../../static/images/310.jpg);
			background-size: 100% 60%;
			filter: blur(2rpx);
		}

		@-webkit-keyframes move {
			0% {
				transform: translateY(0);
			}

			100% {
				transform: translateY(-4741rpx);
			}
		}

		@keyframes move {
			0% {
				transform: translateY(0);
			}

			100% {
				transform: translateY(-4741rpx);
			}
		}

	}

	.img-captcha {
		width: 150upx;
		height: 60upx;
	}

	button[type="primary"] {
		background-color: $color-primary;
		border-radius: 0;
		font-size: 34upx;
		margin-top: 60upx;
	}

	.logo-box {
		padding-top: 115rpx;
		position: fixed;
		top: 35%;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;

		.logo-item {
			display: block;
			width: 400rpx;
			height: 66rpx;
			margin: 0 auto 120rpx;
			color: white;
			font-size: 70rpx;
			font-weight: 700;
			text-align: center;
		}

		.logo-btn {
			width: 418rpx;
			height: 88rpx;
			background-color: #fff;
			box-shadow: 0 2rpx 16rpx 0 rgba(0, 0, 0, .08);
			border-radius: 88rpx;
			font-size: 28rpx;
			line-height: 88rpx;
			color: #93b1b1;
			display: flex;
			-webkit-box-align: center;
			align-items: center;
			-webkit-box-pack: center;
			justify-content: center;

			.iconfont {
				font-size: 40rpx;
				margin-right: 20rpx;
			}

			.weixin {
				width: 10rpx;
				height: 10rpx;
				padding-left: 2rpx;
				line-height: 40rpx;
				font-size: 28rpx;
				border-radius: 50%;
				margin-right: 10rpx;
				color: #fff;
				background-color: #49b44a;
			}
		}

		.cancel-login {
			font-size: 28rpx;
			height: 60rpx;
			line-height: 60rpx;
			color: #fff;
			text-align: center;
			margin-top: 20rpx;
			z-index: 5;
		}

		.login-title-info {
			font-size: 20rpx;
			color: #fff;
			width: 500rpx;
			margin-top: 100rpx;
			text-align: center;
		}
	}

	.login-form {
		padding: 40rpx;
	}

	button[type="primary"] {
		background-color: $color-primary;
		border-radius: 0;
		font-size: 34upx;
		margin-top: 60upx;
	}

	.links {
		text-align: center;
		margin-top: 40upx;
		font-size: 26upx;
		color: #434343;

		view {
			display: inline-block;
			vertical-align: top;
			margin: 0 10upx;
		}

		.link-highlight {
			color: #434343;
		}
	}
</style>
